import {
  useCommiteeActivateModuleMutation,
  useCommitteeDashboardMasterQuery,
  useCommitteeAddAuthorityMutation,
  useCommitteeAuthorityListQuery,
  useIqacDashboardMasterQuery,
  useIqacAddMemberMutation,
  useIqacMemberListQuery,
  useIqacAddSyllabusMutation,
  useIqacAddNestedSyllabusMutation,
  useIqacAddDocumentMutation,
  useIqacAddNaacIqacDocumentMutation,
} from "../../../Services/SiteManager/siteManagerApiDynamic";

export const useCommiteeActivateModule = () => {
  const [commiteeActivateModule] = useCommiteeActivateModuleMutation();
  return [commiteeActivateModule];
};

export const useCommitteeDashboardMaster = (args) => {
  const { data, refetch, isFetching } = useCommitteeDashboardMasterQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    committeeDashboardMaster: data,
    committeeDashboardMasterRefetch: refetch,
    committeeDashboardMasterLoading: isFetching,
  };
};

export const useCommitteeAddAuthority = () => {
  const [committeeAddAuthority] = useCommitteeAddAuthorityMutation();
  return [committeeAddAuthority];
};
